        "description": "\t\t\tКартонна коробка - це досить універсальний пакувальний матеріал. Він використовується в багатьох сфера торгівлі і виробництва. Найчастіше в картонні коробки упаковують продукти харчування, побутові електроприлади і лікеро-горілчану продукцію. При виготовленні коробок необхідно дотримуватися кількох правил:\n\n\t1) Продукція повинна відповідати вимогам і побажанням замовника.\n\t2) Картонні коробки повинні виготовлятись з використанням мікрогофри і гофрокартону високої якості, адже якість матеріалу пізніше впливає на якість всього виробу.\n\t3) Картонні коробки виготовляються різних розмірів та форм, в них можуть поміститися невеликі продукти харчування або масивна побутова техніка (телевізор, наприклад).\n\t4) На картонні коробки, спеціалізованим друкованим устаткуванням, може бути нанесено зображення за бажанням замовника. Зображення може бути кольоровим, двоколірним або одноколірним.\n\t5) Продукція, що виготовляється повинна відповідати вимогам ГОСТу .\n\t6) Продукція, що виготовляється повинна бути екологічно чистою, не містити шкідливих домішок і не забруднювати навколишнє середовище і атмосферу.\t\tГоловною перевагою картонних коробок є їх невисока ціна, при високій якості. Використання сучасних технологій при виробництві картонної упаковки забезпечить Вам найвищий рівень попиту і виробництва. До таких технологій відносяться:\n\n\t\t• лазерна різка готової продукції\n\t\t• використання у виробництві масляних і водо-дисперсійних фарб\n\t\t• готова продукція піддається ультрафіолетовому лакуванню\n\t\t• використання каширування при виробництві\n\t\t• метод висікання, що дозволяє сформувати будь-яку конфігурацію коробки\n\n\tПідіб'ємо підсумок - в сучасному світі найкращим бізнесом у виробництві пакувальної продукції, безсумнівно, є виробництво картонних коробок. У них вельми низька собівартість, а попит на них формується різними виробниками харчової та алкогольної продукції, а також електроніки.\n\n\tТаким чином, картонна упаковка є кращим вибором для упаковки готових виробів. Знайти що не-будь, що буде поєднувати в собі всі переваги цього пакувального матеріалу, при мінімальній наявності недоліків, буде вельми проблематично.",
